/***** Flex Variables *****/

[class|="flex"] {
  --flex-neg-mar-tn: -2px;
  --flex-pos-mar-tn: 2px;
  --flex-width-tn: 4px;
  --flex-neg-mar-sm: -5px;
  --flex-pos-mar-sm: 5px;
  --flex-width-sm: 10px;
  --flex-neg-mar-md: -10px;
  --flex-pos-mar-md: 10px;
  --flex-width-md: 20px;
  --flex-neg-mar-lg: -20px;
  --flex-pos-mar-lg: 20px;
  --flex-width-lg: 40px;
  --flex-neg-mar-wd: -50px;
  --flex-pos-mar-wd: 50px;
  --flex-width-wd: 100px;
}

/***** Flex, Col & Row *****/
[class|="flex"],
[class|="col"],
[class|="row"] {
  display: flex;
  flex-wrap: wrap;
}

[class|="col"] {
  --col-pad: 0 10px;

  flex-direction: column;
  padding: var(--col-pad);
}

[class|="row"] {
  --row-pad: 10px 0;

  flex-direction: row;
  padding: var(--row-pad);
}

[class|="col"][class*="-reverse"] {
  flex-direction: column-reverse;
}

[class|="row"][class*="-reverse"] {
  flex-direction: row-reverse;
}

[class|="col"][class*="-nowrap"],
[class|="row"][class*="-nowrap"] {
  flex-wrap: nowrap;
}

[class|="col"][class*="-inverse"],
[class|="row"][class*="-inverse"] {
  flex-wrap: wrap-reverse;
}

