[class|="grid"][class*="-12tn"],
[class|="fluid"][class*="-12tn"] {
  grid-template: auto/repeat(12, auto);
}

/***** Grid Gap Tiny *****/
[class|="gap"][class*="-1tn"] {
  grid-gap: var(--gap-1tn);
}

[class|="gap"][class*="-2tn"] {
  grid-gap: var(--gap-2tn);
}

[class|="gap"][class*="-3tn"] {
  grid-gap: var(--gap-3tn);
}

