/********** KEYFRAMES **********/

/*
1. Keyframes Variables
2. Rotate
3. RotateX
4. RotateY
5. Scale
6. ScaleX
7. ScaleY
8. Skew
9. SkewX
10. SkewY
11. Translate
12. TranslateX
13. TranslateY
*/

/***** Keyframes Variables *****/
:root {
  --rotate: rotate(-360deg);
  --rotateX: rotateX(-360deg);
  --rotateY: rotateY(-360deg);
  --scale: scale(0);
  --scaleX: scaleX(0);
  --scaleY: scaleY(0);
  --skew: skew(180deg, 180deg);
  --skewX: skewX(180deg);
  --skewY: skewY(180deg);
  --translate: translate(-100vw, -100vh);
  --translateX: translateX(-100vw);
  --translateY: translateY(-100vh);
}

/***** Rotate *****/
@keyframes rotate {
  to {
    transform: var(--rotate);
  }
}

/***** RotateX *****/
@keyframes rotateX {
  to {
    transform: var(--rotateX);
  }
}

/***** RotateY *****/
@keyframes rotateY {
  to {
    transform: var(--rotateY);
  }
}

/***** Scale *****/
@keyframes scale {
  to {
    transform: var(--scale);
  }
}

/***** ScaleX *****/
@keyframes scaleX {
  to {
    transform: var(--scaleX);
  }
}

/***** ScaleY *****/
@keyframes scaleY {
  to {
    transform: var(--scaleY);
  }
}

/***** Skew *****/
@keyframes skew {
  to {
    transform: var(--skew);
  }
}

/***** SkewX *****/
@keyframes skewX {
  to {
    transform: var(--skewX);
  }
}

/***** SkewY *****/
@keyframes skewY {
  to {
    transform: var(--skewY);
  }
}

/***** Translate *****/
@keyframes translate {
  to {
    transform: var(--translate);
  }
}

/***** TranslateX *****/
@keyframes translateX {
  to {
    transform: var(--translateX);
  }
}

/***** TranslateY *****/
@keyframes translateY {
  to {
    transform: var(--translateY);
  }
}
